<script>
import declarationAPI from './api/declarationAPI.js';
import packinglistAPI from './api/packinglistAPI.js';
import auditingAPI from './api/auditingAPI.js';
//import './mock/declaration.js';
import packinglistTable from './components/packinglistTable.vue';

export default {
  data() {
    return {
      declarationDetailDialogModal: false,
      packinglistDialogModal: false,
      declarationID: '',
      declarationType: '',
      clientWidth: 0,
      clientHeight: 0,
      searchword: '',
      selectedRows: [],
      declarationData: [],
      currentPage: 1,
      pageSizes: [10, 20, 50],
      pageSize: 10,
      total: 0,
      editMode: 1,
      declarationDialogmodel: false,
      tmpDeclaration: {},
      dataLoading: false,
      confirmLoading: false,
      declarationTypeOptions: [
        { key: 'import', value: '' },
        { key: 'export', value: '' },
      ],
      sort: '',
      sortOptions: [
        { key: '', value: '' },
        { key: 'declarationtype', value: '' },
        { key: 'preentrynumber', value: '' },
        { key: 'importorexportport', value: '/' },
        { key: 'managementunit', value: '' },
        { key: 'declarationunit', value: '' },
        { key: 'declarationdate', value: '' },
      ],
      auditStatus: '',
      auditStatusOptions: [
        { key: '', value: '' },
        { key: 'W', value: '' },
        { key: 'Y', value: '' },
        { key: 'N', value: '' },
      ],
      retrieval: '',
      retrievalOptions: [
        { key: '', value: '' },
        { key: 'declarationtype', value: '' },
        { key: 'preentrynumber', value: '' },
        { key: 'importorexportport', value: '/' },
        { key: 'managementunit', value: '' },
        { key: 'declarationunit', value: '' },
        { key: 'declarationdate', value: '' },
      ],
      logic: '',
      logicOptions: [
        { key: '', value: '' },
        { key: 'and', value: '' },
        { key: 'or', value: '' },
        { key: 'none', value: '' },
      ],
    };
  },
  methods: {
    showPackinglist(id, type) {
      this.declarationID = id;
      this.declarationType = type;
      this.packinglistDialogModal = true;
    },
    getDeclarationData() {
      this.dataLoading = true;
      declarationAPI
        .getDeclaration({})
        .then(data => {
          this.declarationData = data.data;
          this.total = data.total;
          this.dataLoading = false;
        });
    },
    onSelectionChange(selection) {
      this.selectedRows = selection;
    },
    expandRow(row) {
      this.declarationType = row.declarationtype;
      this.declarationID = row.id;
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.getDeclarationData();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getDeclarationData();
    },
    passClick(id) {
      let rowIds = [];
      if (id) {
        rowIds = [id];
      } else {
        this.selectedRows.forEach(function(row) {
          rowIds.push(row.id);
        });
      }
      auditingAPI.doAuditing(id, true).then(data => {
        this.$notify({
          title: '',
          message: data.message,
          type: 'success',
          duration: 2000,
        });
        if (data.status == 1) {
          rowIds.forEach(rowid => {
            let index = this.declarationData.findIndex(val => val.id === rowid);
            this.declarationData[index].auditstatus = 'Y';
            this.declarationData[index].auditstatusname = '';
          });
        }
      });
    },
    notPassClick(id) {
      let rowIds = [];
      if (id) {
        rowIds = [id];
      } else {
        this.selectedRows.forEach(function(row) {
          rowIds.push(row.id);
        });
      }
      auditingAPI.doAuditing(rowIds, false).then(data => {
        this.$notify({
          title: '',
          message: data.message,
          type: 'success',
          duration: 2000,
        });
        if (data.status == 1) {
          rowIds.forEach(rowid => {
            let index = this.declarationData.findIndex(val => val.id === rowid);
            this.declarationData[index].auditstatus = 'N';
            this.declarationData[index].auditstatusname = '';
          });
        }
      });
    },
    viewClick() {
      this.$notify({
        title: '',
        message: '',
        type: 'success',
        duration: 2000,
      });
      this.tmpDeclaration = Object.assign({}, this.selectedRows[0]);
      this.declarationDetailDialogModal = true;
    },
  },
  created() {
    this.clientWidth = document.documentElement.clientWidth - 200;
    this.clientHeight = document.documentElement.clientHeight - 200;
    this.getDeclarationData();
  },
  components: {
    'packinglist-table': packinglistTable,
  },
};
</script>

