an><span v-else></span>{{tmpDeclaration.loadingorfingerport}}</td>\n          <td colspan=\"2\"><span v-if=\"tmpDeclaration.declarationtype == 'import'\"></span><span v-else></span>{{tmpDeclaration.destinationorconsignmentplace}}</td>\n        </tr>\n        <tr class=\"t1\">\n          <td colspan=\"3\"><span></span>{{tmpDeclaration.approvalnumber}}</td>\n          <td colspan=\"1\"><span></span>{{tmpDeclaration.transactionmethod}}</td>\n          <td colspan=\"2\"><span></span>{{tmpDeclaration.freight}}</td>\n          <td colspan=\"2\"><span></span>{{tmpDeclaration.premium}}</td>\n          <td colspan=\"2\"><span></span>{{tmpDeclaration.incidental}}</td>\n        </tr>\n        <tr class=\"t1\">\n          <td colspan=\"3\"><span></span>{{tmpDeclaration.agreementnumber}}</td>\n          <td colspan=\"1\"><span></span>{{tmpDeclaration.goodsnumber}}</td>\n          <td colspan=\"2\"><span></span>{{tmpDeclaration.packagingtype}}</td>\n          <td colspan=\"2\"><span></span>{{tmpDeclaration.grossweight}}</td>\n          <td colspan=\"2\"><span></span>{{tmpDeclaration.netweight}}</td>\n        </tr>\n        <tr class=\"t1\">\n          <td colspan=\"3\"><span></span>{{tmpDeclaration.containernumber}}</td>\n          <td colspan=\"5\"><span></span>{{tmpDeclaration.documentsattached}}</td>\n          <td colspan=\"2\"><span v-if=\"tmpDeclaration.declarationtype == 'import'\"></span><span v-else></span>{{tmpDeclaration.purposeormanufacturer}}</td>\n        </tr>\n        <tr class=\"t2\">\n          <td colspan=\"10\" valign=\"top\"><span></span>{{tmpDeclaration.shippingmarksandremarks}}</td>\n        </tr>\n        <tr>\n          <td colspan=\"10\" valign=\"top\">\n            <packinglist-table :declarationID=\"tmpDeclaration.id\" :declarationType=\"tmpDeclaration.declarationtype\">\n            </packinglist-table>\n          </td>\n        </tr>\n        <tr class=\"t3\">\n          <td colspan=\"10\" valign=\"top\"><span></span>{{tmpDeclaration.taxpaidornot}}</