<template>
  <div>
    <el-dialog size="tiny" title="" :visible.sync="show" @close="esc">
      <el-form label-width="160px" :model="company">
        <el-form-item label="">
          {{company.companyname}}
        </el-form-item>
        <el-form-item label="">
          {{company.address}}
        </el-form-item>
        <el-form-item label="">
          {{company.phone}}
        </el-form-item>
        <el-form-item label="">
          {{company.fax}}
        </el-form-item>
        <el-form-item label="">
          {{company.postcode}}
        </el-form-item>
        <el-form-item label="">
          {{company.bankcreditrating}}
        </el-form-item>
        <el-form-item label="" v-if="company.allocation>0">
          <span>{{ company.allocation }}</span>
        </el-form-item>
        <el-form-item label="" v-if="company.allocation>0">
          <span>{{ company.used }}</span>
        </el-form-item>
        <el-form-item label="">
          <span>{{ company.adduser }}</span>
        </el-form-item>
        <el-form-item label="">
          <span>{{ company.addtime }}</span>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="esc"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  require('../mock/company.js')
  import companyAPI from '../api/companyAPI.js';

  export default {
    data() {
      return {}
    },
    methods: {
      esc() {
        this.$emit('update:show', false);
      }
    },
    //
    //id
    watch: {
      company: function () {
        if (this.company.companyid > 0 && (this.company.companyname == undefined || this.company.companyname == '')) {
          companyAPI.getCompanyDetail(this.company.companyid).then(data => {
            if (data.status == 1) {
              this.company = data.data;
            } else {
              this.$message.error(data.message);
            }
          });
        }
      }
    },
    props: [
      'show',
      'company'
    ]
  }

</script>

<style scoped>


</style>



