<script>
//import './mock/license.js';
import licenseAPI from './api/licenseAPI.js';
import licensegoodsTable from './components/licensegoodsTable.vue';
export default {
  data() {
    return {
      clientHeight: 0,
      clientWidth: 0,
      /**/
      inLicenseData: [],
      /**/
      outLicenseData: [],
      /**/
      inLicenseModel: {
        name: '',
        manager: '',
        description: '',
        starttime: '',
        endtime: '',
      },
      outLicenseModel: {
        name: '',
        manager: '',
        description: '',
        starttime: '',
        endtime: '',
      },
      /**/
      insearch: {
        licensekey: '',
        importandexportcode: '',
        companyname: '',
        type: 'in',
      },
      /**/
      outsearch: {
        licensekey: '',
        importandexportcode: '',
        companyname: '',
        type: 'out',
      },
      /*tab */
      activeName: 'inlicense',
      /* */
      inLicenseRules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        manager: [{ required: true, message: '', trigger: 'blur' }],
      },
      /* */
      inLicenseShow: false,
      /* */
      outLicenseShow: false,
      /* */
      myFileDialogIsShow: false,
      /* */
      fileUploadDialogIsShow: false,
      /* */
      fileViewDialogIsShow: false,
      /* */
      failDialogIsShow: false,
      passDialogIsShow: false,
      loadDialogIsShow: false,
      /*12*/
      editMode: 1,
      /*12*/
      outeditMode: 1,
      /*12 */
      fileType: 1,
      /**/
      confirmLoading: false,
      /**/
      inSelectedRows: [],
      /**/
      outSelectedRows: [],
      /* */
      fileSelectedRows: [],
      /* */
      startdatepicker: '',
      /* */
      enddatepicker: '',
      /* */
      myCurrentPage: 1,
      /**/
      myPageSizes: [5, 10, 15, 20],
      /* */
      myPageSize: 10,
      /* */
      myTotal: 16,
      /* */
      apCurrentPage: 1,
      /**/
      apPageSizes: [5, 10, 15, 20],
      /* */
      apPageSize: 10,
      /* */
      apTotal: 21,
      /* */
      fileTableData: [],
      /* */
      fileList: [],
      /* */
      percentage: 0,
      // i: 0,
      selectedLicensegoodsRow: {},
      tmpLicensegoods: {},
      licensegoodsDialogModal: false,
    };
  },
  methods: {
    addGoodsClick() {
      this.editMode = 0;
      this.tmpLicensegoods = {};
      this.licensegoodsDialogModal = true;
    },
    editGoodsClick() {
      this.editMode = 1;
      this.tmpLicensegoods = Object.assign({}, this.selectedLicensegoodsRow);
      this.licensegoodsDialogModal = true;
    },
    deleteGoodsClick() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action !== 'confirm') {
            done();
          }
          instance.confirmButtonLoading = true;

          return licenseAPI
            .deleteLicenseGoods(this.selectedLicensegoodsRow.id)
            .then(data => {
              instance.confirmButtonLoading = false;
              done(data);
            });
        },
      }).then(data => {
          this.selectedLicensegoodsRow = [];
          this.$notify({
            title: '',
            message: '',
            type: 'success',
            duration: 2000,
          });
        }).catch(() => {
          this.$notify.error({
            title: '',
            message: '',
            duration: 2000,
          });
        });
    },
    licensegoodsConfirm() {
      if (this.editMode == 1) {
        licenseAPI.updateLicenseGoods(this.tmpLicensegoods).then(data => {
          if (data.status == 1) {
            this.$notify({
              title: '',
              message: data.message,
              type: 'success',
              duration: 2000,
            });
          }
          this.licensegoodsDialogModal = false;
        });
      } else {
        licenseAPI.addLicenseGoods(this.tmpLicensegoods).then(data => {
          if (data.status == 1) {
            this.$notify({
              title: '',
              message: data.message,
              type: 'success',
              duration: 2000,
            });
          }
          this.licensegoodsDialogModal = false;
        });
      }
    },
    licensegoodsTableRowClick(row) {
      this.selectedLicensegoodsRow = row;
    },
    /**/
    inAddClick() {
      this.editMode = 1;
      this.inLicenseModel = {
        name: '',
        manager: '',
        description: '',
        starttime: '',
        endtime: '',
        type: 'in',
      };
      this.inLicenseShow = true;
      this.selectedLicensegoodsRow=[];
    },
    /**/
    inEditClick() {
      this.editMode = 2;
      this.inLicenseModel = Object.assign(
        {},
        this.inLicenseModel,
        this.inSelectedRows[0]
      );
      this.inLicenseShow = true;
      this.selectedLicensegoodsRow=[];
    },
    /**/
    inDeleteClick() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action !== 'confirm') {
            done();
            return;
          }
          this.confirmLoading = true;
          let ids = [];
          this.inSelectedRows.forEach(function(row) {
            ids.push(row.id);
          });
          let idstr = ids.join();
          return licenseAPI.deleteLicense(idstr)
            .then(data => {
              this.confirmLoading = false;
              done(data);
              this.inLicenseData = this.inLicenseData.filter(
                val => !ids.includes(val.id)
              );
            });
        },
      }).then(data => {
          this.inSelectedRows = [];
          this.loadInlicenseList();
          this.$notify({
            title: '',
            message: '',
            type: 'success',
            duration: 2000,
          });
        })
    },

    /**/
    outAddClick() {
      this.outeditMode = 1;
      this.outLicenseModel = {
        name: '',
        manager: '',
        description: '',
        starttime: '',
        endtime: '',
        type: 'out',
      };
      this.outLicenseShow = true;
      this.selectedLicensegoodsRow=[];
    },
    /**/
    outEditClick() {
      this.outeditMode = 2;
      this.outLicenseModel = Object.assign(
        {},
        this.outLicenseModel,
        this.outSelectedRows[0]
      );
      this.outLicenseShow = true;
      this.selectedLicensegoodsRow=[];
    },
    /**/
    outDeleteClick() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action !== 'confirm') {
            done();
            return;
          }
          this.confirmLoading = true;
          this.inSelectedRows.forEach(function(row) {
            ids.push(row.id);
          });
          let idstr = ids.join();
          return licenseAPI.deleteLicense(idstr)
            .then(data => {
              this.confirmLoading = false;
              done(data);
              this.outLicenseData = this.outLicenseData.filter(
                val => !ids.includes(val.id)
              );
            });
        },
      }).then(data => {
          this.outSelectedRows = [];
          this.loadOutlicenseList();
          this.$notify({
            title: '',
            message: '',
            type: 'success',
            duration: 2000,
          });
        })
    },

    /**/
    inOnSelectionChange(selection) {
      this.inSelectedRows = selection;
    },
    /* */
    inOkHandler() {
      let validateForm = () => {
        return new Promise((resolve, reject) => {
          this.$refs['inLicenseRef'].validate(valid => {
            if (valid) {
              return resolve(true);
            }
            return reject(false);
          });
        });
      };
      let addForm = (res) => {
        licenseAPI.addLicense(this.inLicenseModel).then(data => {
          if (data.status == 1) {
            this.$notify({
              title: '',
              message: '',
              type: 'success',
              duration: 2000,
            });
          } else {
            this.$notify({
              title: '',
              message: data.message,
              type: 'error',
              duration: 2000
            });
          }
        });
      };

      let editForm = (res) => {
        let index = this.inLicenseData.findIndex(
          val => val.id === this.inLicenseModel.id
        );
        licenseAPI.updateLicense(this.inLicenseModel).then(data => {
          if (data.status == 1) {
            this.$notify({
              title: '',
              message: '',
              type: 'success',
              duration: 2000,
            });
          } else {
            this.$notify({
              title: '',
              message: data.message,
              type: 'error',
              duration: 2000
            });
          }
        });
      };

      validateForm().then(() => {
          this.confirmLoading = true;
          if (this.editMode === 1) {
            return addForm();
          }
          if (this.editMode === 2) {
            return editForm();
          }
        }).then(res =>{
          this.confirmLoading = false;
          this.inLicenseShow = false;
          this.inLicenseShow = false;
          this.loadInlicenseList();
        });
    },

    /* */
    outOkHandler() {
      let validateForm = () => {
        return new Promise((resolve, reject) => {
          this.$refs['outLicenseRef'].validate(valid => {
            if (valid) {
              return resolve(true);
            }
            return reject(false);
          });
        });
      };
      let outaddForm = () => {
        licenseAPI.addLicense(this.outLicenseModel).then(data => {
          if (data.status == 1) {
            this.$notify({
              title: '',
              message: '',
              type: 'success',
              duration: 2000,
            });
          } else {
            this.$notify({
              title: '',
              message: data.message,
              type: 'error',
              duration: 2000
            });
          }
        });
      };

      let outeditForm = () => {
        let index = this.outLicenseData.findIndex(
          val => val.id === this.outLicenseModel.id
        );
        licenseAPI.updateLicense(this.outLicenseModel).then(data => {
          if (data.status == 1) {
            this.$notify({
              title: '',
              message: '',
              type: 'success',
              duration: 2000,
            });
          } else {
            this.$notify({
              title: '',
              message: data.message,
              type: 'error',
              duration: 2000
            });
          }
        });
      };

      validateForm()
        .then(() => {
          this.confirmLoading = true;
          if (this.outeditMode === 1) {
            return outaddForm();
          }
          if (this.outeditMode === 2) {
            return outeditForm();
          }
        })
        .then(res => {
          this.confirmLoading = false;
          this.outLicenseShow = false;
          this.outLicenseShow = false;
           this.loadOutlicenseList();
        });
    },

    /**/
    outOnSelectionChange(selection) {
      this.outSelectedRows = selection;
    },

    /* */
    fileUpload() {
      this.fileUploadDialogIsShow = true;
    },
    /* */
    fileUploadOkHandler() {
      this.$notify({
        title: '',
        message: '',
        type: 'success',
        duration: 2000,
      });
      this.fileUploadDialogIsShow = false;
    },
    /* */
    onUploadSuccess(response, file) {
      this.$notify({
        title: file.name,
        message: '',
        type: 'success',
        duration: 2000,
      });
    },
    /* */
    fileDownload() {
      this.$notify({
        title: '',
        message: '',
        type: 'success',
        duration: 2000,
      });
    },
    /* */
    fileView() {
      this.fileViewDialogIsShow = true;
    },
    /* */
    fileDelete() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      })
        .then(() => {
          let ids = [];
          this.fileSelectedRows.forEach(function(row) {
            ids.push(row.id);
          });
          return ids;
        })
        .then(ids => {
          this.fileTableData = this.fileTableData.filter(
            val => !ids.includes(val.id)
          );
          this.$notify.success({
            title: '',
            message: '',
            duration: 2000,
          });
        });
    },
    /* */
    loadInlicenseList() {
      let pagedata = {
        pageindex: this.myCurrentPage,
        pagesize: this.myPageSize,
      };
      let search = Object.assign({}, this.insearch, pagedata);
      licenseAPI.getInlicenseList(search).then(data => {
        this.inLicenseData = data.data;
        this.myTotal = data.total;
      });
    },
    /* */
    loadOutlicenseList() {
      let pagedata = {
        pageindex: this.myCurrentPage,
        pagesize: this.myPageSize,
      };
      let search = Object.assign({}, this.outsearch, pagedata);
      licenseAPI.getOutlicenseList(search).then(data => {
        this.outLicenseData = data.data;
        this.apTotal = data.total;
      });
    },

    /* */
    myHandleSizeChange(val) {
      this.myPageSize = val;
      this.loadInlicenseList();
    },
    /* */
    myHandleCurrentChange(val) {
      this.myCurrentPage = val;
      this.loadInlicenseList();
    },
    /* */
    apHandleSizeChange(val) {
      this.apPageSize = val;
      this.loadOutlicenseList();
    },
    /* */
    apHandleCurrentChange(val) {
      this.apCurrentPage = val;
      this.loadOutlicenseList();
    },
  },
  created() {
    this.clientHeight = document.documentElement.clientHeight - 270;
    let num = Math.floor(this.clientHeight / 40) - 1;
    this.apPageSize = Math.floor(num / 5) * 5;
    this.apPageSizes = [
      this.apPageSize,
      this.apPageSize * 2,
      this.apPageSize * 4,
    ];
    this.myPageSize = Math.floor(num / 5) * 5;
    this.myPageSizes = [
      this.myPageSize,
      this.myPageSize * 2,
      this.myPageSize * 4,
    ];
    this.loadInlicenseList();
    this.loadOutlicenseList();
  },
  components: {
    'licensegoods-table': licensegoodsTable,
  },
};
</script>
<style>
.projPage .el-tabs__header {
  margin-bottom: 0;
}

input[type='file'].el-upload__input {
  display: none !important;
  margin-bottom: -20px;
}
</style>

<style scoped>
.main-content-wrap {
  padding: 10px;
}
