  padding-top: 10px;
  padding-bottom: 10px;
}

.demo-table-expand {
  font-size: 12px;
}

.demo-table-expand label {
  color: #99a9bf;
}

.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 47%;
}

.e-form .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 45%;
}

.filelist-toolbar {
  margin-left: 1px;
}

.fileView-content {
  width: 100%;
  border: 1px solid #ccc;
  padding: 5px;
  line-height: 20px;
}

.input-320 {
  width: 320px;
}

.form-title {
  font-size: 24px;
  font-weight: bold;
  margin-left: 6%;
  padding: 10px 0 5px 0;
}

.form-panel {
  width: 90%;
  margin-left: 5%;
  padding: 20px 0 0 0;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: #fff;
}

.goods-panel {
  width: 90%;
  margin-left: 5%;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: #fff;
}
</style>



