idth: 0,\n          showLookDialog: false,\n          selectedRows: [],\n          taxCuttingRules: {\n            largetype: [\n              {\n                trigger: 'change', required: true,\n                validator: (rule, value, callback) => {\n                  if (value) {\n                    callback();\n                  } else {\n                    callback(new Error(''));\n                  }\n                }\n              }\n            ],\n            smalltype: [\n              {\n                trigger: 'change', required: true,\n                validator: (rule, value, callback) => {\n                  if (value) {\n                    callback();\n                  } else {\n                    callback(new Error(''));\n                  }\n                }\n              }\n            ],\n            way: [\n              {\n                trigger: 'change', required: true,\n                validator: (rule, value, callback) => {\n                  if (value) {\n                    callback();\n                  } else {\n                    callback(new Error(''));\n                  }\n                }\n              }\n            ],\n            starttime: [\n              {trigger: 'change', required: true,\n                validator: (rule, value, callback) => {\n                  if (value) {\n                    callback();\n                  } else {\n                    callback(new Error(''));\n                  }\n                }\n              }\n            ],\n            endtime: [\n              {trigger: 'change', required: true,\n                validator: (rule, value, callback) => {\n                  if (value) {\n                    callback();\n                  } else {\n                    callback(new Error(''));\n                  }\n                }\n              }\n            ],\n            policypapertitle: [\n              { required: true, message: '', trigger: 'blur' },\n              { min: 1,