 <el-button class=\"z-toolbar-btn\" :plain=\"true\" :disabled=\"selectedLicensegoodsRow.length === 0\" @click=\"deleteGoodsClick\">\n              <i class=\"fa fa-remove\"></i></el-button>\n          </div>\n          <licensegoods-table :licenseID = \"outLicenseModel.id\" @row-click=\"licensegoodsTableRowClick\"></licensegoods-table>\n        </div>\n      </el-form>\n      <div slot=\"footer\">\n        <el-button @click=\"outLicenseShow=false\"></el-button>\n        <el-button type=\"primary\" @click=\"outOkHandler\" :loading=\"confirmLoading\"> </el-button>\n      </div>\n    </el-dialog>\n    <!--  -->\n    <el-dialog :title=\"fileType===1?'':''\" :visible.sync=\"myFileDialogIsShow\">\n      <el-toolbar class=\"filelist-toolbar\">\n        <el-button class=\"z-toolbar-btn\" :plain=\"true\" @click=\"fileUpload\">\n          <i class=\"fa fa-upload\"></i></el-button>\n        <el-button class=\"z-toolbar-btn\" :disabled=\"fileSelectedRows.length < 1\" :plain=\"true\" @click=\"fileDownload\">\n          <i class=\"fa fa-download\"></i></el-button>\n        <el-button class=\"z-toolbar-btn\" :disabled=\"fileSelectedRows.length !== 1\" :plain=\"true\" @click=\"fileView\">\n          <i class=\"fa fa-search\"></i></el-button>\n        <el-button class=\"z-toolbar-btn\" :plain=\"true\" :disabled=\"fileSelectedRows.length < 1\" @click=\"fileDelete\">\n          <i class=\"fa fa-trash-o\"></i></el-button>\n      </el-toolbar>\n      <el-table ref=\"fileTabelRef\" :data=\"fileTableData\" tooltip-effect=\"dark\" style=\"width: 100%;margin-top:5px;\" @selection-change=\"fileOnSelectionChange\">\n        <el-table-column type=\"selection\" width=\"60\" align=\"center\"></el-table-column>\n        <el-table-column prop=\"name\" label=\"\"></el-table-column>\n      </el-table>\n    </el-dialog>\n    <!--  -->\n    <el-dialog :title=\"''\" :visible.sync=\"fileUploadDialogIsShow\">\n      <el-upload :on-success=\"onUploadSuccess\" class=\"upload-file\" accept=\".jpg,.png\" action=\"\" :multiple=\"false\" :file-list=\"fileList\">\n        <el-button size=\"small\" type=\"primary\"></el-button>\n        <div slot=\"tip\" class=\"el-upload__tip\">jpg/png500kb</div>\n      </el-upload>\n      <div slot=\"footer\">\n        <el-button @click=\"fileUploadDialogIsShow=false\"></el-button>\n        <el-button type=\"primary\" @click=\"fileUploadOkHandler\"> </el-button>\n      </div>\n    </el-dialog>\n    <el-dialog :title=\"editMode==1? '': ''\" :visible.sync=\"licensegoodsDialogModal\" :close-on-click-modal=\"false\">\n      <el-form label-position=\"right\" :model=\"tmpLicensegoods\" inline label-width=\"200px\">\n        <el-form-item label=\"\">\n          <el-input class=\"e-input\" v-model=\"tmpLicensegoods.specification\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"\">\n          <el-input class=\"e-input\" v-model=\"tmpLicensegoods.unit\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"\">\n          <el-input class=\"e-input\" v-model=\"tmpLicensegoods.quantity\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"USD\">\n          <el-input class=\"e-input\" v-model=\"tmpLicensegoods.unitprice\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"USD\">\n          <el-input class=\"e-input\" v-model=\"tmpLicensegoods.amount\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"\">\n          <el-input class=\"e-input\" v-model=\"tmpLicensegoods.amountinusd\"></el-input>\n        </el-form-item>\n      </el-form>\n      <div slot=\"footer\">\n        <el-button @click=\"licensegoodsDialogModal = false\"> </el-button>\n        <el-button type=\"primary\" @click=\"licensegoodsConfirm\"> </el-button>\n      </div>\n    </el-dialog>\n  </div>\n</template>\n\n<script>\n//import './mock/license.js';\nimport licenseAPI from './api/licenseAPI.js';\nimport licensegoodsTable from './components/licensegoodsTable.vue';\nexport default {\n  data() {\n    return {\n      clientHeight: 0,\n      clientWidth: 0,\n      /**/\n      inLicenseData: [],\n      /**/\n      outLicenseData: [],\n      /**/\n      inLicenseModel: {\n        name: '',\n        manager: '',\n        description: '',\n        starttime: '',\n        endtime: '',\n      },\n      outLicenseModel: {\n        name: '',\n        manager: '',\n        description: '',\n        starttime: '',\n        endtime: '',\n      },\n      /**/\n      insearch: {\n        licensekey: '',\n        importandexportcode: '',\n        companyname: '',\n        type: 'in',\n      },\n      /**/\n      outsearch: {\n        licensekey: '',\n        importandexportcode: '',\n        companyname: '',\n        type: 'out',\n      },\n      /*tab */\n      activeName: 'inlicense',\n      /* */\n      inLicenseRules: {\n        name: [{ required: true, message: '', trigger: 'blur' }],\n        manager: [{ required: true, message: '', trigger: 'blur' }],\n      },\n      /* */\n      inLicenseShow: false,\n      /* */\n      outLicenseShow: false,\n      /* */\n      myFileDialogIsShow: false,\n      /* */\n      fileUploadDialogIsShow: false,\n      /* */\n      fileViewDialogIsShow: false,\n      /* */\n      failDialogIsShow: false,\n      passDialogIsShow: false,\n      loadDialogIsShow: false,\n      /*12*/\n      editMode: 1,\n      /*12*/\n      outeditMode: 1,\n      /*12 */\n      fileType: 1,\n      /**/\n      confirmLoading: false,\n      /**/\n      inSelectedRows: [],\n      /**/\n      outSelectedRows: [],\n      /* */\n      fileSelectedRows: [],\n      /* */\n      startdatepicker: '',\n      /* */\n      enddatepicker: '',\n      /* */\n      myCurrentPage: 1,\n      /**/\n      myPageSizes: [5, 10, 15, 20],\n      /* */\n      myPageSize: 10,\n      /* */\n      myTotal: 16,\n      /* */\n      apCurrentPage: 1,\n      /**/\n      apPageSizes: [5, 10, 15, 20],\n      /* */\n      apPageSize: 10,\n      /* */\n      apTotal: 21,\n      /* */\n      fileTable