import axios from 'axios'
import AxiosMockAdapter from 'axios-mock-adapter'
window.axiosMock =
  window.axiosMock || new AxiosMockAdapter(axios, { delayResponse: 10 })

const manifestsData = [
  {
    id: 1,
    manifestnum: 'QWERT1',
    receivecompany: '',
    goodsname: ',,',
    sendaddress: '',
    receiveperson: '',
    telephone: '010-34235345'
  },
  {
    id: 2,
    manifestnum: 'TREWQ2',
    receivecompany: '',
    goodsname: '',
    sendaddress: '',
    receiveperson: '',
    telephone: '010-888888'
  },
  {
    id: 3,
    manifestnum: 'ASDFG3',
    receivecompany: '',
    goodsname: '',
    sendaddress: '',
    receiveperson: '',
    telephone: '010-66666'
  },
  {
    id: 4,
    manifestnum: 'KJHFGFG4',
    receivecompany: 'H&M',
    goodsname: '',
    sendaddress: '',
    receiveperson: '',
    telephone: '010-09824545'
  }
]

function random (items) {
  return items[parseInt(items.length * Math.random())]
}
axiosMock.onGet('/api/manifests').reply(200, { data: manifestsData })
axiosMock.onPost(/api\/manifests$/).reply(200, { status: 1, message: '' })
axiosMock
  .onPut(/api\/manifests\/\d+$/)
  .reply(200, { status: 1, message: '' })
axiosMock
  .onDelete(/api\/manifests\/.+$/)
  .reply(200, { status: 1, message: '' })

var manifestGoodsInfo = [
  'T()T ()()',
  '',
  'BSV.',
  '/'
]
axiosMock.onGet(/api\/manifests\/viewgoods\/\d+$/).reply(config => {
  let goodsInfo = random(manifestGoodsInfo)
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      resolve([
        200,
        {
          data: goodsInfo
        }
      ])
    }, 500)
  })
})



