          </el-date-picker>
        </el-form-item>
        </div>
        <div style="height:100px;"></div>
      </el-form>
    </div>
    <el-dialog :title="editMode==1? '': ''" :visible.sync="packingdetailDialogModal" :close-on-click-modal="false">
      <el-form label-position="right" :model="tmpPacking" inline label-width="200px">
        <el-form-item label="">
          <el-input class="e-input" v-model="tmpPacking.id"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input class="e-input" type="textarea" :rows="3" v-model="tmpPacking.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input class="e-input" v-model="tmpPacking.number"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input class="e-input" v-model="tmpPacking.singleprice"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input class="e-input" v-model="tmpPacking.totalprice"></el-input>
        </el-form-item>
        <el-form-item v-if="this.declarationType == 'import'" label="">
          <el-input class="e-input" v-model="tmpPacking.productcountry"></el-input>
        </el-form-item>
        <el-form-item v-else label="">
          <el-input class="e-input" v-model="tmpPacking.productcountry"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input class="e-input" v-model="tmpPacking.currency"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input class="e-input" v-model="tmpPacking.exemption"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="packingdetailDialogModal = false"> </el-button>
        <el-button type="primary" @click="packingdetailConfirm"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import declarationAPI from './api/declarationAPI.js';
import packinglistAPI from './api/packinglistAPI.js';
//import './mock/declaration.js';
import packinglistTable from './components/packinglistTable.vue';

export default {
  data() {
    return {
      searchDeclaration: {},
      advancedSearchModal: true,
      tmpPacking: {},
      packingdetailDialogModal: false,
      packinglistDialogModal: false,
      declarationType: '',
      declarationID: '',
      clientWidth: 0,
      clientHeight: 0,
      searchword: '',
      selectedRows: [],
      selectedPackingRow: [],
      declarationData: [],
      currentPage: 1,
      pageSizes: [10, 20, 50],
      pageSize: 10,
      total: 0,
      editMode: 1,
      declarationDialogmodel: false,
      tmpDeclaration: {},
      dataLoading: false,
      confirmLoading: false,
      declarationTypeOptions: [
        { key: 'import', value: '' },
        { key: 'export', value: '' },
      ],
      sort: '',
      sortOptions: [
        { key: '', value: '' },
        { key: 'declarationtype', value: '' },
        { key: 'customsnumber', value: '' },
        { key: 'importorexportport', value: '/' },
        { key: 'declarationunit', value: '' },
        { key: 'declarationdate', value: '' },
        { key: 'entrydate', value: '' },
      ],
      retrieval: '',
      retrievalOptions: [
        { key: '', value: '' },
        { key: 'customsnumber', value: '' },
        { key: 'declarationunit', value: '' },
      ],
      logic: '',
      logicOptions: [
        { key: '', value: '' },
        { key: 'and', value: '' },
        { key: 'or', value: '' },
        { key: 'none', value: '' },
      ],
    };
  },
  methods: {
    advanceSearch() {
      this.advancedSearchModal = true;
    },
    doAdvanceSearch() {
      this.advancedSearchModal = false;
    },
    addPackingClick() {
      this.editMode = 0;
      this.tmpPacking = {};
      this.packingdetailDialogModal = true;
    },
    editPackingClick() {
      this.editMode = 1;
      this.tmpPacking = Object.assign({}, this.selectedPackingRow);
      this.packingdetailDialogModal = true;
    },
    deletePackingClick() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action !== 'confirm') {
            done();
          }
          instance.confirmButtonLoading = true;

          return packinglistAPI
            .deletePackingList(this.selectedPackingRow.id)
            .then(data => {
              instance.confirmButtonLoading = false;
              console.log(data);
              done(data);
            });
        },
      })
        .then(data => {
          this.selectedPackingRow = [];
          this.$notify({
            title: '',
            message: '',
            type: 'success',
            duration: 2000,
          });
        })
        .catch(() => {
          this.$notify.error({
            title: '',
            message: '',
            duration: 2000,
          });
        });
    },
    packingdetailConfirm() {
      if (this.editMode == 1) {
        packinglistAPI.updatePackingList(this.tmpPacking).then(data => {
          if (data.status == 1) {
            this.$notify({
              title: '',
              message: data.message,
              type: 'success',
              duration: 2000,
            });
          }
          this.packingdetailDialogModal = false;
        });
      } else {
        packinglistAPI.addPackingList(this.tmpPacking).then(data => {
          if (data.status == 1) {
            this.$notify({
              title: '',
              message: data.message,
              type: 'success',
              duration: 2000,
            });
          }
          this.packingdetailDialogModal = false;
        });
      }
    },
    packingRowClick(row) {
      console.log(row);
      this.selectedPackingRow = row;
    },
    showPackinglist(id, type) {
      console.log(id);
      console.log(type);
      this.declarationID = id;
      this.declarationType = type;
      this.selectedPackingRow = [];
      this.packinglistDialogModal = true;
    },
    getDeclarationData() {
      this.dataLoading = true;
      declarationAPI
        .getDeclaration(this.searchDeclaration)
        .then(data => {
          console.log(data);
          this.declarationData = data.data;
          this.total = data.total;
          this.dataLoading = false;
        });
    },
    onSelectionChange(selection) {
      this.selectedRows = selection;
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.getDeclarationData();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getDeclarationData();
    },
    expandRow(row) {
      this.declarationType = row.declarationtype;
      this.declarationID = row.id;
    },
    addClick() {
      this.editMode = 0;
      this.tmpDeclaration = {
        declarationtype: 'import',
      };
      this.declarationDialogmodel = true;
      this.selectedPackingRow = [];
    },
    editClick() {
      this.editMode = 1;
      this.tmpDeclaration = Object.assign({}, this.selectedRows[0]);
      this.declarationDialogmodel = true;
      this.selectedPackingRow = [];
    },
    deleteClick() {
      let rowIds = [];
      this.selectedRows.forEach(function(row) {
        rowIds.push(row.id);
      });
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action !== 'confirm') {
            done();
          }
          instance.confirmButtonLoading = true;

          return declarationAPI.deleteDeclaration(rowIds).then(data => {
            instance.confirmButtonLoading = false;
            done(data);
          });
        },
      })
        .then(data => {
          this.declarationData = this.declarationData.filter(
            val => !rowIds.includes(val.id)
          );
          this.selectedRows = [];
          this.$notify({
            title: '',
            message: '',
            type: 'success',
            duration: 2000,
          });
        })
        .catch(() => {
          this.$notify.error({
            title: '',
            message: '',
            duration: 2000,
          });
        });
    },
    returnMain() {
      this.declarationDialogmodel = false;
    },
    confirm() {
      if (this.editMode == 1) {
        declarationAPI.updateDeclaration(this.tmpDeclaration).then(data => {
          if (data.status == 1) {
            this.$notify({
              title: '',
              message: data.message,
              type: 'success',
              duration: 2000,
            });
          }
          let index = this.declarationData.findIndex(
            val => val.id === this.tmpDeclaration.id
          );
          this.declarationData = [
            ...this.declarationData.slice(0, index),
            Object.assign({}, this.tmpDeclaration),
            ...this.declarationData.slice(index + 1),
          ];
        });
      } else {
        declarationAPI.addDeclaration(this.tmpDeclaration).then(data => {
          if (data.status == 1) {
            this.$notify({
              title: '',
              message: data.message,
              type: 'success',
              duration: 2000,
            });
          }
          this.declarationData = [
            ...this.declarationData,
            Object.assign({}, this.tmpDeclaration, { id: data.declaration.id }),
          ];

          this.tmpDeclaration = {
            declarationtype: 'import',
          };
        });
      }
    },
  },
  created() {
    this.clientWidth = document.documentElement.clientWidth - 200;
    this.clientHeight = document.documentElement.clientHeight - 200;
    this.getDeclarationData();
  },
  components: {
    'packinglist-table': packinglistTable,
  },
};
</script>

<style scoped>
.main-content-wrap {
  padding: 10px;
}

.search-bar {
  width: 100%;
  text-align: right;
  padding-bottom: 10px;
}

.demo-table-expand {
  font-size: 12px;
}

.demo-table-expand label {
  color: #99a9bf;
}

.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 45%;
}

.page-wrap {
  width: 100%;
  text-align: right;
  position: relative;
  top: 5px;
  padding-right: 10px;
}

.e-input {
  width: 270px;
}

.search-select {
  width: 150px;
}

.form-title {
  font-size: 24px;
  font-weight: bold;
  margin-left: 6%;
  padding: 20px 0 5px 0;
}
.form-panel {
  width: 80%;
  margin-left: 5%;
  padding: 20px 0 0 0;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: #fff;
}

.s-input {
  width: 400px;
}

.search-form-title {
  padding: 20px 0;
}
.packinglist-panel {
  width: 80%;
  margin-left: 5%;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: #fff;
}
</style>



