ut>\n        </el-form-item>\n        <el-form-item label=\"\">\n          <el-input class=\"e-input\" type=\"textarea\" :rows=\"3\" v-model=\"tmpPacking.name\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"\">\n          <el-input class=\"e-input\" v-model=\"tmpPacking.number\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"\">\n          <el-input class=\"e-input\" v-model=\"tmpPacking.singleprice\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"\">\n          <el-input class=\"e-input\" v-model=\"tmpPacking.totalprice\"></el-input>\n        </el-form-item>\n        <el-form-item v-if=\"this.declarationType == 'import'\" label=\"\">\n          <el-input class=\"e-input\" v-model=\"tmpPacking.productcountry\"></el-input>\n        </el-form-item>\n        <el-form-item v-else label=\"\">\n          <el-input class=\"e-input\" v-model=\"tmpPacking.productcountry\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"\">\n          <el-input class=\"e-input\" v-model=\"tmpPacking.currency\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"\">\n          <el-input class=\"e-input\" v-model=\"tmpPacking.exemption\"></el-input>\n        </el-form-item>\n      </el-form>\n      <div slot=\"footer\">\n        <el-button @click=\"packingdetailDialogModal = false\"> </el-button>\n        <el-button type=\"primary\" @click=\"packingdetailConfirm\"> </el-button>\n      </div>\n    </el-dialog>\n  </div>\n</template>\n\n<script>\nimport declarationAPI from './api/declarationAPI.js';\nimport packinglistAPI from './api/packinglistAPI.js';\n//import './mock/declaration.js';\nimport packinglistTable from './components/packinglistTable.vue';\n\nexport default {\n  data() {\n    return {\n      searchDeclaration: {},\n      advancedSearchModal: true,\n      tmpPacking: {},\n      packingdetailDialogModal: false,\n      packinglistDialogModal: false,\n      declarationType: '',\n      declarationID: '',\n      clientWidth: 0,\n      clientHeight: 0,\n      searchword: '',\n      selectedRows: [],\n      selectedPackingRow: [],\n      declarationData: [],\n      currentPage: 1,\n      pageSizes: [10, 20, 50],\n      pageSize: 10,\n      total: 0,\n      editMode: 1,\n      declarationDialogmodel: false,\n      tmpDeclaration: {},\n      dataLoading: false,\n      confirmLoading: false,\n      declarationTypeOptions: [\n        { key: 'import', value: '' },\n        { key: 'export', value: '' },\n      ],\n      sort: '',\n      sortOptions: [\n        { key: '', value: '' },\n        { key: 'declarationtype', value: '' },\n        { key: 'customsnumber', value: '' },\n        { key: 'importorexportport', value: '/' },\n        { key: 'declarationunit', value: '' },\n        { key: 'declarationdate', value: '' },\n        { key: 'entrydate', value: '' },\n      ],\n      retrieval: '',\n      retrievalOptions: [\n        { key: '', value: '' },\n        { key: 'customsnumber', value: '' },\n        { key: 'declarationunit', value: '' },\n      ],\n      logic: '',\n      logicOptions: [\n        { key: '', value: '' },\n        { key: 'and', value: '' },\n        { key: 'or', value: '' },\n        { key: 'none', value: '' },\n      ],\n    };\n  },\n  methods: {\n    advanceSearch() {\n      this.advancedSearchModal = true;\n    },\n    doAdvanceSearch() {\n      this.advancedSearchModal = false;\n    },\n    addPackingClick() {\n      this.editMode = 0;\n      this.tmpPacking = {};\n      this.packingdetailDialogModal = true;\n    },\n    editPackingClick() {\n      this.editMode = 1;\n      this.tmpPacking = Object.assign({}, this.selectedPackingRow);\n      this.packingdetailDialogModal = true;\n    },\n    deletePackingClick() {\n      this.$confirm('', '', {\n        confirmButtonText: '',\n        cancelButtonText: '',\n        type: 'warning',\n        beforeClose: (action, instance, done) => {\n          if (action !== 'confirm') {\n            done();\n          }\n          instance.confirmButtonLoading = true;\n\n          return packinglistAPI\n            .deletePackingList(this.selectedPackingRow.id)\n            .then(data => {\n              instance.confirmButtonLoading = false;\n              console.log(data);\n              done(data);\n            });\n        },\n      })\n        .then(data => {\n          this.selectedPackingRow = [];\n          this.$notify({\n            title: '',\n            message: '',\n            type: 'success',\n            duration: 2000,\n          });\n        })\n        .catch(() => {\n          this.$notify.error({\n            title: '',\n            message: '',\n            duration: 2000,\n          });\n        });\n    },\n    packingdetailConfirm() {\n      if (this.editMode == 1) {\n        packinglistAPI.updatePackingList(this.tmpPacking).then(data => {\n          if (data.status == 1) {\n            this.$notify({\n              title: '',\n              message: data.message,\n              type: 'success',\n              duration: 2000,\n            });\n          }\n          this.packingdetailDialogModal = false;\n        });\n      } else {\n        packinglistAPI.addPackingList(this.tmpPacking).then(data => {\n          if (data.status == 1) {\n            this.$notify({\n              title: '',\n              message: data.message,\n              type: 'success',\n              duration: 2000,\n            });\n          }\n          this.packingdetailDialogModal = false;\n        });\n      }\n    },\n    packingRowClick(row) {\n      console.log(row);\n      this.selectedPackingRow = row;\n    },\n    showPackinglist(id, type) {\n      console.log(id);\n      console.log(type);\n      this.declarationID = id;\n      this.declarationType = type;\n      this.selectedPackingRow = [];\n      this.packinglistDialogModal = true;\n    },\n    getDeclarationData() {\n      this.dataLoading = true;\n      declarationAPI\n        .getDeclaration